<template>
	<view class="uni-forms">
		<form>
			<slot></slot>
		</form>
	</view>
</template>

<script>
	import Validator from './validate.js';
	import {
		deepCopy,
		getValue,
		isRequiredField,
		setDataValue,
		getDataValue,
		realName,
		isRealName,
		rawData,
		isEqual
	} from './utils.js'

	// #ifndef VUE3
	// 
	import Vue from 'vue';
	Vue.prototype.binddata = function(name, value, formName) {
		if (formName) {
			this.$refs[formName].setValue(name, value);
		} else {
			let formVm;
			for (let i in this.$refs) {
				const vm = this.$refs[i];
				if (vm && vm.$options && vm.$options.name === 'uniForms') {
					formVm = vm;
					break;
				}
			}
			if (!formVm) return console.error(' uni-froms  ref ');
			formVm.setValue(name, value);
		}
	};
	// #endif
	/**
	 * Forms 
	 * @description 
	 * @tutorial https://ext.dcloud.net.cn/plugin?id=2773
	 * @property {Object} rules	
	 * @property {String} validateTrigger = [bind|submit|blur]	  submit
	 * @value bind		
	 * @value submit	
	 * @value blur	  
	 * @property {String} labelPosition = [top|left]	label   left
	 * @value top		 label
	 * @value left	 label
	 * @property {String} labelWidth	label  65px
	 * @property {String} labelAlign = [left|center|right]	label    left
	 * @value left		label 
	 * @value center	label 
	 * @value right		label 
	 * @property {String} errShowType = [undertext|toast|modal]	
	 * @value undertext	
	 * @value toast			toast
	 * @value modal			modal
	 * @event {Function} submit	
	 * @event {Function} validate	
	 */
	export default {
		name: 'uniForms',
		emits: ['validate', 'submit'],
		options: {
			virtualHost: true
		},
		props: {
			// 
			value: {
				type: Object,
				default () {
					return null;
				}
			},
			// vue3  value 
			modelValue: {
				type: Object,
				default () {
					return null;
				}
			},
			// 1.4.0  v-model  value  modelValue
			model: {
				type: Object,
				default () {
					return null;
				}
			},
			// 
			rules: {
				type: Object,
				default () {
					return {};
				}
			},
			//  undertext  [undertext|toast|modal]
			errShowType: {
				type: String,
				default: 'undertext'
			},
			//   bind  [bind|submit]
			validateTrigger: {
				type: String,
				default: 'submit'
			},
			// label  left   top/left
			labelPosition: {
				type: String,
				default: 'left'
			},
			// label 
			labelWidth: {
				type: [String, Number],
				default: ''
			},
			// label  left  left/center/right
			labelAlign: {
				type: String,
				default: 'left'
			},
			border: {
				type: Boolean,
				default: false
			}
		},
		provide() {
			return {
				uniForm: this
			}
		},
		data() {
			return {
				// 
				formData: {},
				formRules: {}
			};
		},
		computed: {
			// 
			localData() {
				const localVal = this.model || this.modelValue || this.value
				if (localVal) {
					return deepCopy(localVal)
				}
				return {}
			}
		},
		watch: {
			//  ,
			// localData: {},
			// 
			rules: {
				handler: function(val, oldVal) {
					this.setRules(val)
				},
				deep: true,
				immediate: true
			}
		},
		created() {
			// #ifdef VUE3
			let getbinddata = getApp().$vm.$.appContext.config.globalProperties.binddata
			if (!getbinddata) {
				getApp().$vm.$.appContext.config.globalProperties.binddata = function(name, value, formName) {
					if (formName) {
						this.$refs[formName].setValue(name, value);
					} else {
						let formVm;
						for (let i in this.$refs) {
							const vm = this.$refs[i];
							if (vm && vm.$options && vm.$options.name === 'uniForms') {
								formVm = vm;
								break;
							}
						}
						if (!formVm) return console.error(' uni-froms  ref ');
						formVm.setValue(name, value);
					}
				}
			}
			// #endif

			// 
			this.childrens = []
			// TODO  uni-data-picker ,
			this.inputChildrens = []
			this.setRules(this.rules)
		},
		methods: {
			/**
			 * 
			 *  
			 * @param {Array} rules 
			 */
			setRules(rules) {
				// TODO  
				this.formRules = Object.assign({}, this.formRules, rules)
				// 
				this.validator = new Validator(rules);
			},

			/**
			 * 
			 *   
			 * @param {Object} key
			 * @param {Object} value
			 */
			setValue(key, value) {
				let example = this.childrens.find(child => child.name === key);
				if (!example) return null;
				this.formData[key] = getValue(key, value, (this.formRules[key] && this.formRules[key].rules) || [])
				return example.onFieldChange(this.formData[key]);
			},

			/**
			 * 
			 * 
			 * 
			 * @param {Array} keepitem 
			 * @param {type} callback 
			 */
			validate(keepitem, callback) {
				return this.checkAll(this.formData, keepitem, callback);
			},

			/**
			 * 
			 * 
			 * @param {Array|String} props 
			 * @param {Function} 
			 */
			validateField(props = [], callback) {
				props = [].concat(props);
				let invalidFields = {};
				this.childrens.forEach(item => {
					const name = realName(item.name)
					if (props.indexOf(name) !== -1) {
						invalidFields = Object.assign({}, invalidFields, {
							[name]: this.formData[name]
						});
					}
				});
				return this.checkAll(invalidFields, [], callback);
			},

			/**
			 * 
			 *  prop  prop 
			 * @param {Array|String} props  
			 */
			clearValidate(props = []) {
				props = [].concat(props);
				this.childrens.forEach(item => {
					if (props.length === 0) {
						item.errMsg = '';
					} else {
						const name = realName(item.name)
						if (props.indexOf(name) !== -1) {
							item.errMsg = '';
						}
					}
				});
			},

			/**
			 *  
			 * 
			 * 
			 * @param {Array} keepitem 
			 * @param {type} callback 
			 */
			submit(keepitem, callback, type) {
				for (let i in this.dataValue) {
					const itemData = this.childrens.find(v => v.name === i);
					if (itemData) {
						if (this.formData[i] === undefined) {
							this.formData[i] = this._getValue(i, this.dataValue[i]);
						}
					}
				}

				if (!type) {
					console.warn('submit validate');
				}

				return this.checkAll(this.formData, keepitem, callback, 'submit');
			},

			// 
			async checkAll(invalidFields, keepitem, callback, type) {
				//  
				if (!this.validator) return
				let childrens = []
				// item
				for (let i in invalidFields) {
					const item = this.childrens.find(v => realName(v.name) === i)
					if (item) {
						childrens.push(item)
					}
				}

				// validatefunciont ,
				if (!callback && typeof keepitem === 'function') {
					callback = keepitem;
				}

				let promise;
				//  Promise 
				if (!callback && typeof callback !== 'function' && Promise) {
					promise = new Promise((resolve, reject) => {
						callback = function(valid, invalidFields) {
							!valid ? resolve(invalidFields) : reject(valid);
						};
					});
				}

				let results = [];
				//  
				let tempFormData = JSON.parse(JSON.stringify(invalidFields))
				// , for   awiat
				for (let i in childrens) {
					const child = childrens[i]
					let name = realName(child.name);
					const result = await child.onFieldChange(tempFormData[name]);
					if (result) {
						results.push(result);
						// toast ,modal 
						if (this.errShowType === 'toast' || this.errShowType === 'modal') break;
					}
				}


				if (Array.isArray(results)) {
					if (results.length === 0) results = null;
				}
				if (Array.isArray(keepitem)) {
					keepitem.forEach(v => {
						let vName = realName(v);
						let value = getDataValue(v, this.localData)
						if (value !== undefined) {
							tempFormData[vName] = value
						}
					});
				}

				// TODO submit 
				if (type === 'submit') {
					this.$emit('submit', {
						detail: {
							value: tempFormData,
							errors: results
						}
					});
				} else {
					this.$emit('validate', results);
				}

				// const resetFormData = rawData(tempFormData, this.localData, this.name)
				let resetFormData = {}
				resetFormData = rawData(tempFormData, this.name)
				callback && typeof callback === 'function' && callback(results, resetFormData);

				if (promise && callback) {
					return promise;
				} else {
					return null;
				}

			},

			/**
			 * validate
			 * @param {Object} result
			 */
			validateCheck(result) {
				this.$emit('validate', result);
			},
			_getValue: getValue,
			_isRequiredField: isRequiredField,
			_setDataValue: setDataValue,
			_getDataValue: getDataValue,
			_realName: realName,
			_isRealName: isRealName,
			_isEqual: isEqual
		}
	};
</script>

<style lang="scss" scoped>
	.uni-forms {}
</style>
